  renderAnalytics() {
    if (this.analytics.length === 0) {
      return `
        <div class="empty-state">
          <i class="fas fa-chart-bar"></i>
          <p>ŸÑÿß ÿ™Ÿàÿ¨ÿØ ÿ®ŸäÿßŸÜÿßÿ™ ÿ™ÿ≠ŸÑŸäŸÑŸäÿ©</p>
        </div>
      `;
    }

    return `
      <div class="analytics-dashboard">
        ${this.analytics
          .map(
            analytic => `
          <div class="analytic-card">
            <div class="analytic-header">
              <h3>${analytic.metric}</h3>
              <span class="analytic-value">${analytic.value}</span>
            </div>
            <div class="analytic-body">
              <p>${analytic.description || ''}</p>
            </div>
          </div>
        `
          )
          .join('')}
      </div>
    `;
  }

  getFilteredData(data) {
    let filtered = [...data];
    if (this.filters.status !== 'all') {
      filtered = filtered.filter(item => item.status === this.filters.status);
    }
    if (this.filters.severity !== 'all') {
      filtered = filtered.filter(item => item.severity === this.filters.severity);
    }
    if (this.filters.search) {
      const search = this.filters.search.toLowerCase();
      filtered = filtered.filter(
        item =>
          (item.title && item.title.toLowerCase().includes(search)) ||
          (item.name && item.name.toLowerCase().includes(search)) ||
          (item.description && item.description.toLowerCase().includes(search))
      );
    }
    return filtered;
  }

  async loadData() {
    if (!this.useAPI) {
      this.loadFromLocalStorage();
      return;
    }

    try {
      if (!this.connectionManager.isFullyConnected()) {
        this.loadFromLocalStorage();
        return;
      }

      const [incidents, risks, policies, trainings, equipment, inspections, analytics] =
        await Promise.all([
          this.apiClient.get(this.API_ENDPOINTS.incidents || '/api/advanced-security/incidents'),
          this.apiClient.get(this.API_ENDPOINTS.risks || '/api/advanced-security/risks'),
          this.apiClient.get(this.API_ENDPOINTS.policies || '/api/advanced-security/policies'),
          this.apiClient.get(this.API_ENDPOINTS.trainings || '/api/advanced-security/trainings'),
          this.apiClient.get(this.API_ENDPOINTS.equipment || '/api/advanced-security/equipment'),
          this.apiClient.get(
            this.API_ENDPOINTS.inspections || '/api/advanced-security/inspections'
          ),
          this.apiClient.get(this.API_ENDPOINTS.analytics || '/api/advanced-security/analytics'),
        ]);

      this.incidents = incidents.data || [];
      this.risks = risks.data || [];
      this.policies = policies.data || [];
      this.trainings = trainings.data || [];
      this.equipment = equipment.data || [];
      this.inspections = inspections.data || [];
      this.analytics = analytics.data || [];

      this.saveToLocalStorage();
      this.updateContent();
    } catch (error) {
      console.error('Error loading security data:', error);
      this.loadFromLocalStorage();
    }
  }

  setupRealtimeSync() {
    if (!this.realtimeSync) return;
    this.realtimeSync.subscribe('advanced-security', '*', update => {
      if (update.action === 'create' || update.action === 'update' || update.action === 'delete') {
        this.loadData();
      }
    });
  }

  setupConnectionMonitoring() {
    if (!this.connectionManager) return;
    this.connectionManager.on('online', () => {
      this.loadData();
    });
  }

  switchView(view) {
    this.currentView = view;
    this.updateContent();
  }

  handleFilterChange(type, event) {
    this.filters[type] = event.target.value;
    this.updateContent();
  }

  handleSearch(event) {
    this.filters.search = event.target.value;
    this.updateContent();
  }

  updateContent() {
    const content = document.getElementById('securityContent');
    if (content) {
      content.innerHTML = this.renderCurrentView();
    }
  }

  getStatusText(status) {
    const statusMap = {
      reported: 'ŸÖÿ®ŸÑÿ∫ ÿπŸÜŸá',
      investigating: 'ŸÇŸäÿØ ÿßŸÑÿ™ÿ≠ŸÇŸäŸÇ',
      resolved: 'ŸÖÿ≠ŸÑŸàŸÑ',
      closed: 'ŸÖÿ∫ŸÑŸÇ',
      active: 'ŸÜÿ¥ÿ∑',
      inactive: 'ÿ∫Ÿäÿ± ŸÜÿ¥ÿ∑',
      scheduled: 'ŸÖÿ¨ÿØŸàŸÑ',
      completed: 'ŸÖŸÉÿ™ŸÖŸÑ',
      passed: 'ŸÜÿ¨ÿ≠',
      failed: 'ŸÅÿ¥ŸÑ',
      valid: 'ÿµÿßŸÑÿ≠',
      expired: 'ŸÖŸÜÿ™ŸáŸä',
    };
    return statusMap[status] || status;
  }

  getSeverityText(severity) {
    const severityMap = {
      low: 'ŸÖŸÜÿÆŸÅÿ∂',
      medium: 'ŸÖÿ™Ÿàÿ≥ÿ∑',
      high: 'ÿπÿßŸÑŸä',
      critical: 'ÿ≠ÿ±ÿ¨',
    };
    return severityMap[severity] || severity;
  }

  formatDate(date) {
    if (!date) return 'ÿ∫Ÿäÿ± ŸÖÿ≠ÿØÿØ';
    return new Date(date).toLocaleDateString('ar-SA');
  }

  formatDateTime(dateTime) {
    if (!dateTime) return 'ÿ∫Ÿäÿ± ŸÖÿ≠ÿØÿØ';
    return new Date(dateTime).toLocaleString('ar-SA');
  }

  saveToLocalStorage() {
    try {
      localStorage.setItem('advancedIncidents', JSON.stringify(this.incidents));
      localStorage.setItem('advancedRisks', JSON.stringify(this.risks));
      localStorage.setItem('advancedPolicies', JSON.stringify(this.policies));
      localStorage.setItem('advancedTrainings', JSON.stringify(this.trainings));
      localStorage.setItem('advancedEquipment', JSON.stringify(this.equipment));
      localStorage.setItem('advancedInspections', JSON.stringify(this.inspections));
      localStorage.setItem('advancedAnalytics', JSON.stringify(this.analytics));
    } catch (error) {
      console.error('Error saving to localStorage:', error);
    }
  }

  loadFromLocalStorage() {
    try {
      this.incidents = JSON.parse(localStorage.getItem('advancedIncidents') || '[]');
      this.risks = JSON.parse(localStorage.getItem('advancedRisks') || '[]');
      this.policies = JSON.parse(localStorage.getItem('advancedPolicies') || '[]');
      this.trainings = JSON.parse(localStorage.getItem('advancedTrainings') || '[]');
      this.equipment = JSON.parse(localStorage.getItem('advancedEquipment') || '[]');
      this.inspections = JSON.parse(localStorage.getItem('advancedInspections') || '[]');
      this.analytics = JSON.parse(localStorage.getItem('advancedAnalytics') || '[]');
    } catch (error) {
      console.error('Error loading from localStorage:', error);
    }
  }

  setupEventListeners() {
    this.createIncident = this.createIncident.bind(this);
    this.createRisk = this.createRisk.bind(this);
    this.switchView = this.switchView.bind(this);
    this.handleFilterChange = this.handleFilterChange.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.viewIncident = this.viewIncident.bind(this);
    this.investigateIncident = this.investigateIncident.bind(this);
    this.resolveIncident = this.resolveIncident.bind(this);
  }

